<template>
  <section>
    <template v-for="alert in alerts">
      <b-alert :show="alert.dismiss" dismissible :variant="alert.type" :key="alerts.indexOf(alert)">
        <h4>
          <i v-if="alert.type === 'success' || alert.type === 'warning' " class="icon fa fa-check"></i>
          <i class="icon fa fa-ban" v-else></i>{{alert.type}}!</h4>
        {{alert.message}}
      </b-alert>
    </template>
    <b-modal id="myModal" ref="myModalRef" :title="`Add New Deck for ${selectedUser.name}`" v-model="showModal" @ok="addDeck(selectedUser.id, selectedDeck)">
      <b-input-group prepend="Deck">
      <b-form-input v-model="selectedDeck"></b-form-input>
      </b-input-group>
    </b-modal>
    <main>
      <b-container fluid>
        <header class="section-header main-header text-center">
          <b-row>
            <b-col cols="12" xs="12">
              <b-img fluid src="logo.png" style="width: 10%"/>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" xs="12">
              <h2>Modern Liga 2019</h2>
              <p>Let's play Magic like Garfield meant it</p>
            </b-col>
          </b-row>
        </header>
        <hr>
        <!--<b-row>
          <b-col md="12" xs="12">
            <create-user @refresh="getUsers"></create-user>
          </b-col>
        </b-row>
        <hr>-->
        <b-row>
        <b-col xs="12">
          <b-table striped hover responsive :items="items" :fields="fields" sort-by.sync="position">
            <template slot="username" slot-scope="data">
              <b-row>
                <b-col><router-link :to="`user/results/${data.item.id}` ">{{data.item.username}}</router-link></b-col>
                <!--<b-col><b-btn class="btn btn-sm btn-info ml-5" @click="prepareModal(data.item.username, data.item.id)"> + deck</b-btn></b-col>-->
              </b-row>
            </template>
            <template slot="payout" slot-scope="data">
              <i class="fa fa-euro"></i> {{payout[data.index]}}
            </template>
            <template slot="topPositions" slot-scope="data">
                <span v-for="(pos, index) in data.item.topPositions">{{pos.points}}/{{pos.position}}{{index+1 < data.item.topPositions.length ? ', ':''}}</span>
            </template>
            <template slot="sameDeckWins" slot-scope="data">
              Deck: {{data.item.sameDeckWins.deck}}, Points: {{data.item.sameDeckWins.points}}
            </template>
          </b-table>
        </b-col>
      </b-row>
        <hr>
      <b-row>
        <b-col cols="4" md="4">
          <router-link to="newResult"><button class="btn btn-info">New Result</button></router-link>
        </b-col>
        <!--<b-col cols="4" md="4">
          <router-link to="newEvent"><button class="btn btn-info">New Event</button></router-link>
        </b-col>-->
      </b-row>
      <hr/>
      <b-row>
        <b-col>
          <h4>Latest Results</h4>
          <b-table striped hover responsive :items="results">
          </b-table>
        </b-col>
      </b-row>
    </b-container>
    </main>
  </section>
</template>

<script>
  /* eslint-disable */
  import Api from '@/services/Api'
  export default {
    name: 'app',
    data () {
      return {
        selectedDeck: '',
        showModal: false,
        selectedUser: {},
        fields: [
          {
            key: 'position',
            sortable: true
          }, {
            key: 'payout',
            sortable: false
          }, {
            key: 'username',
            sortable: true,
            label: 'Player Name'
          }, {
            key: 'points',
            sortable: true,
            label: 'Total Points'
          }, {
            key: 'avgPoints',
            sortable: true,
            label: 'Ø Points'
          }, {
            key: 'topPositions',
            sortable: true,
            label: 'TOP 9 Pts/Plz'
         /* }, {
            key: 'worstPosition',
            sortable: true,
            label: 'Worst Position TOP 9'*/
          /*}, {
            key: 'gamesPlayed',
            sortable: true,
            label: 'Games Played'*/
          }, {
            key: 'gamesWon',
            sortable: true,
            label: 'GW (%)'
          }, {
            key: 'decks',
            sortable: true,
            label: 'Total Decks'
          }, {
            key: 'sameDeckWins',
            sortable: true,
            label: 'Same Deck Points'
          }
        ],
        users: [],
        items: [],
        alerts: [],
        results: [],
        payout: [135, 100, 70, 50, 45, 35, 30, 25, 20, 10, 5, 5, 0, 0]
      }
    },
    methods: {
      registerUser () {
        Api().post('users/register', {firstName: this.username.split(' ')[0], lastName: this.username.split(' ')[1], email: this.email, password: this.password, ageGroup: this.ageGroup})
          .then(res => {
            this.isLoggedIn = true
          })
          .catch(error => {
            this.alerts.push({type: 'danger', dismiss: 4, message: error.response.data.message})
          })
      },
      getLatestResults () {
        Api().get('results/latest')
          .then(res => {
            res.data.forEach(r => {
              r.deck = r.deck.name
              r.user = r.user.username
              r.event = r.event.name
              delete r._v
              delete r.id
              this.results.push(r)
            })
          })
      },
      getUsers () {
        Api().get('users')
          .then(res => {
            this.items = []
            res.data.forEach(u => {
              let user = {
                position: res.data.indexOf(u) + 1,
                decks: 0,
                gamesWon: 0,
                gamesPlayed: 0,
                worstPosition: 0,
                topPositions: [],
                avgPoints: 0,
                points: 0,
                username: u.username,
                sameDeckWins: 0
              }
              user.id = u._id
              user.worstPosition = this.getWorstPosition(u.results)
              //here goes eq to calculate points based on results/top/worst
              this.calculateTopPositions(user, u.results)
              this.items.push(user)
              this.items.sort((a, b) => {
                if (a.points === b.points) {
                  if (a.gamesWon === b.gamesWon) {
                    return a.avgPoints - b.avgPoints
                  }
                  return a.gamesWon - b.gamesWon
                }
                return a.points - b.points
              })
              this.items.reverse()
              this.items.forEach(u => {
                u.position = this.items.indexOf(u) + 1
              })
            })
          })
      },
      getSameDeckWins (decks, user) {
        let result = []
        decks.reduce((res, d) => {
          if (!res[d.deck]) {
            res[d.deck] = {deck: d.deck, points: 0}
            result.push(res[d.deck])
          }
          res[d.deck].points += d.points
          return res
        }, {})
        let max = Math.max.apply(Math, result.map(o => {return o.points}))
        user.decks = result.length
        return result[result.findIndex(x => x.points == max)]
      },
      calculateTopPositions (user, res) {
        if (res.length === 0) {
         return true
        }
        let decks = []
        res.forEach(r => {
          user.gamesWon += parseFloat(r.gwpct)
          decks.push({deck: r.deck.name, points: r.points})
        })
        user.sameDeckWins = this.getSameDeckWins(decks, user)
        user.gamesWon /= res.length
        res.sort((r1, r2) => {
          if (r1.points > r2.points) return -1
          if (r1.points < r2.points) return 1
          if (r1.points === r2.points) {
            if (r1.position > r2.position) return 1
            if (r1.position < r2.position) return -1
          }
        })
        if (res.length > 9) {
          for (let i = 0; i < 9; i++) {
            user.topPositions.push({points: res[i].points, position: res[i].position})
          }
        } else {
          for (let i in res) {
            user.topPositions.push({points: res[i].points, position: res[i].position})
          }
        }
        user.topPositions.forEach(tp => {
          user.points += tp.points
        })
        user.avgPoints = user.points / user.topPositions.length
      },
      onlyUnique (value, index, self) {
        return self.indexOf(value) === index
      },
      getWorstPosition (results) {
        let max = Math.max.apply(Math, results.map(o => {return o.position}))
        console.log(results.findIndex(x => x.position == max))
      },
      prepareModal(user, id) {
        this.selectedUser = {name: user, id: id}
        this.showModal = true
      },
      addDeck (id, deck) {
        Api().post('decks/create', {user: id, name: deck})
          .then(res => {
            this.getUsers()
          })
      }
    },
    mounted () {
      this.getUsers()
      this.getLatestResults()
    }
  }
</script>
