<template>
  <section>
    <b-modal id="myModal2" ref="myModalRef" :title="`Add New Deck for ${user.username}`" v-model="showModal" @ok="addDeck(user.id, selectedDeck)">
      <b-input-group prepend="Deck">
        <b-form-input v-model="selectedDeck"></b-form-input>
      </b-input-group>
    </b-modal>
    <main>
      <b-container fluid>
        <template v-for="alert in alerts">
          <b-alert :show="alert.dismiss" dismissible :variant="alert.type" :key="alerts.indexOf(alert)">
            <h4>
              <i v-if="alert.type === 'success' || alert.type === 'warning' " class="icon fa fa-check"></i>
              <i class="icon fa fa-ban" v-else></i>{{alert.type}}!</h4>
            {{alert.message}}
          </b-alert>
        </template>
        <header class="section-header text-center">
          <h2>User Info:</h2>
          <p>Check how much you sucked</p>
        </header>
        <hr>
        <div class="container bootstrap snippet">
          <div class="row">
            <div class="col-sm-10"><h1><img style="max-width: 3%" src="/pw-symbol.png"/> {{user.username}}</h1></div>
          </div>
          <div class="row">
            <div class="col-sm-3"><!--left col-->
              <div class="text-center">
                <b-img thumbnail fluid src="/jace.jpg"/>
               <!-- <h6>Upload a different photo...</h6>
                <input type="file" class="text-center center-block file-upload">-->
              </div>
              <hr>
              <span><b-col><b-btn class="btn btn-sm btn-info ml-5" @click="showModal=true">Add new deck</b-btn></b-col></span>
              <br>
              <ul class="list-group">
                <li class="list-group-item text-muted"><i class="ss ss-uma ss-3x ss-grad"></i> League Data</li>
                <li class="list-group-item text-right"><span class="pull-left"><strong>Total Points</strong></span><br/><span class="text-muted">{{calculatedUser.points}}</span></li>
                <li class="list-group-item text-right"><span class="pull-left"><strong> Ø Points</strong></span><br/><span class="text-muted"> {{calculatedUser.avgPoints}}</span></li>
                <li class="list-group-item text-right"><span class="pull-left"><i class=""></i><strong>Decks Played</strong></span><br/> <span class="text-muted">{{calculatedUser.decks}}</span></li>
                <li class="list-group-item text-right"><span class="pull-left"><strong>Most wins with Same Deck</strong></span><br/> <span class="text-muted"> Deck: {{calculatedUser.sameDeckWins.deck}}, Points: {{calculatedUser.sameDeckWins.points}}</span></li>
                <li class="list-group-item text-right"><span class="pull-left"><i class="fa fa-percent fa-1x"></i><strong> Games Won</strong></span><br/><span class="text-muted"> {{calculatedUser.gamesWon}}</span></li>
                <li class="list-group-item text-right"><span class="pull-left"><i class="fa fa-thumbs-up fa-1x"></i><strong> Top 9</strong></span><br/><span class="text-muted"> <span v-for="(pos, index) in calculatedUser.topPositions">{{pos.points}}/{{pos.position}}{{index+1 < calculatedUser.topPositions.length ? ', ':''}}</span></span></li>
              </ul>
              <div class="panel panel-default">
                <!--<div class="panel-heading">Social Media</div>
                <div class="panel-body">
                  <i class="fa fa-facebook fa-2x"></i> <i class="fa fa-github fa-2x"></i> <i class="fa fa-twitter fa-2x"></i> <i class="fa fa-pinterest fa-2x"></i> <i class="fa fa-google-plus fa-2x"></i>
                </div>-->
              </div>
            </div><!--/col-3-->
            <div class="col-sm-9">
              <!--<ul class="nav nav-tabs">
                <li class="active"><a data-toggle="tab" href="#home">Home</a></li>
                <li><a data-toggle="tab" href="#messages">Menu 1</a></li>
                <li><a data-toggle="tab" href="#settings">Menu 2</a></li>
              </ul>-->
              <b-tabs fill tabs>
                <b-tab title="Info" active>
                  <b-table striped hover responsive :items="user.results" :fields="['event', 'deck', 'points', 'position', 'gwpct']" sort-by.sync="position">
                    <template slot="event" slot-scope="data">
                      <b-row>
                        <b-col>
                          Name: {{data.item.event.name}}<br>
                          Date: {{data.item.event.date}}<br>
                          Format: {{data.item.event.format}}
                        </b-col>
                      </b-row>
                    </template>
                    <template slot="deck" slot-scope="data">
                      {{user.decks.filter(obj => { return obj._id === data.item.deck})[0].name}}
                    </template>
                    <template slot="gwpct" slot-scope="data">
                      {{data.item.gwpct}} %
                    </template>
                    <template slot="topPositions" slot-scope="data">
                      <span v-for="(pos, index) in data.item.topPositions">{{pos.points}}/{{pos.position}}{{index+1 < data.item.topPositions.length ? ', ':''}}</span>
                    </template>
                    <template slot="sameDeckWins" slot-scope="data">
                      Deck: {{data.item.sameDeckWins.deck}}, Points: {{data.item.sameDeckWins.points}}
                    </template>
                  </b-table>
                </b-tab>
                <b-tab title="Most Played Decks">
                  <canvas id="deckStats"></canvas>
                </b-tab>
              </b-tabs><!--/tab-pane-->
            </div><!--/tab-content-->
          </div><!--/col-9-->
        </div>
      </b-container>
    </main>
  </section>
</template>

<script>
  /* eslint-disable */
  import Chart from 'chart.js';
  import Api from "../services/Api";
  export default {
    name: 'addProduct',
    data () {
      return {
        user: {},
        showModal: false,
        calculatedUser:  {
          decks: 0,
          gamesWon: 0,
          gamesPlayed: 0,
          worstPosition: 0,
          topPositions: [],
          avgPoints: 0,
          points: 0,
          sameDeckWins: 0
        },
        selectedDeck: '',
        alerts: [],
        id: this.$route.params.id,
        decks: []
      }
    },
    methods: {
      post () {
        Api().post('results/create', this.result)
          .then(res => {
            this.alerts.push({type: 'success', dismiss: 4, message: 'Result posted succesfully!'})
          })
          .catch(error => {
            this.alerts.push({type: 'danger', dismiss: 4, message: error.response.data.message})
          })
      },
      getSameDeckWins (decks, user) {
        let result = []
        decks.reduce((res, d) => {
          if (!res[d.deck]) {
            res[d.deck] = {deck: d.deck, points: 0}
            result.push(res[d.deck])
          }
          res[d.deck].points += d.points
          return res
        }, {})
        let max = Math.max.apply(Math, result.map(o => {return o.points}))
        user.decks = result.length
        return result[result.findIndex(x => x.points == max)]
      },
      getDecks () {
        console.log(this.result.user)
       /* Api().get(`events/byUser/${this.result.user}`)
          .then(res => {
            this.events = res.data
            let user = this.users.find(x => x.id === this.result.user)
            this.decks = user.decks
          })*/
      },
      getUser () {
        Api().get(`users/${this.id}`)
          .then(res => {
            this.user = res.data
            this.calculateTopPositions(this.calculatedUser, this.user.results)
            this.createChart('deckStats')
          })
          .catch(error => {
            this.alerts.push({type: 'danger', dismiss: 4, message: error.response.data.message})
          })
      },
      createChart (chartId) {
        const ctx = document.getElementById(chartId)
        let dataForDataset = {}
        this.decks.forEach(d => {
          dataForDataset[d.deck] = (dataForDataset[d.deck] || 0) + 1
        })
        let finalData = {
          data: [],
          labels: [],
          backgroundColor: []
        }
        Object.keys(dataForDataset).forEach(item => {
          finalData.labels.push(item)
          finalData.data.push(dataForDataset[item])
          finalData.backgroundColor.push(`rgba(${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, ${Math.floor(Math.random() * 255)}, .5)`)
        })
        const myChart = new Chart(ctx, {
          type: 'doughnut',
          data: {
            datasets: [
              {
                label: 'Most played Decks',
                data: finalData.data,
                backgroundColor: finalData.backgroundColor
              }],
            labels: finalData.labels
          },
          options: {}
        })
      },
      calculateTopPositions (user, res) {
        if (res.length === 0) {
          return true
        }
        let decks = []
        res.forEach(r => {
          user.gamesWon += parseFloat(r.gwpct)
          decks.push({deck: this.user.decks.filter(obj => { return obj._id === r.deck})[0].name, points: r.points})
        })
        this.decks = decks
        user.sameDeckWins = this.getSameDeckWins(decks, user)
        user.gamesWon /= res.length
        res.sort((r1, r2) => {
          if (r1.points > r2.points) return -1
          if (r1.points < r2.points) return 1
          if (r1.points === r2.points) {
            if (r1.position > r2.position) return 1
            if (r1.position < r2.position) return -1
          }
        })
        if (res.length > 9) {
          for (let i = 0; i < 9; i++) {
            user.topPositions.push({points: res[i].points, position: res[i].position})
          }
        } else {
          for (let i in res) {
            user.topPositions.push({points: res[i].points, position: res[i].position})
          }
        }
        user.topPositions.forEach(tp => {
          user.points += tp.points
        })
        user.avgPoints = user.points / user.topPositions.length
      },
      addDeck (id, deck) {
        Api().post('decks/create', {user: id, name: deck})
          .then(res => {
            this.alerts.push({type: 'success', dismiss: 4, message: `Deck ${deck} Created Succesfully!`})
            this.result.user.decks.push(res.data.deck)
          })
          .catch(error => {
            this.alerts.push({type: 'danger', dismiss: 4, message: error.response.data.message})
          })
      }
    },
    mounted () {
      this.getUser()
    }
  }
</script>

