<template>
  <section>
    <b-modal id="myModal2" ref="myModalRef" :title="`Add New Deck for ${result.user.username}`" v-model="showModal" @ok="addDeck(result.user.id, selectedDeck)">
      <b-input-group prepend="Deck">
        <b-form-input v-model="selectedDeck"></b-form-input>
      </b-input-group>
    </b-modal>
    <main>
      <b-container fluid>
        <template v-for="alert in alerts">
          <b-alert :show="alert.dismiss" dismissible :variant="alert.type" :key="alerts.indexOf(alert)">
            <h4>
              <i v-if="alert.type === 'success' || alert.type === 'warning' " class="icon fa fa-check"></i>
              <i class="icon fa fa-ban" v-else></i>{{alert.type}}!</h4>
            {{alert.message}}
          </b-alert>
        </template>
        <header class="section-header">
          <h2>Create New Result</h2>
          <p>Load your own results for the event of your choice.</p>
        </header>
        <hr>
          <b-row>
            <b-col cols="12" md="12">
              <label>Event:</label>
              <select class="form-control" v-model="result.event">
                <option selected value="0">----Select One----</option>
                <option v-for="evt in events" :value="evt" :key="evt.id">{{evt.name}}</option>
              </select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="12">
              <label>User:</label>
              <select class="form-control" v-model="result.user">
                <option selected value="0">----Select One----</option>
                <option v-for="user in users" :value="user" :key="user.id">{{user.username}}</option>
              </select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="8">
              <label>Deck:</label> <b-btn size="sm" variant="link" :disabled="result.user === '0'" @click="showModal = true"> Add deck</b-btn>
              <select class="form-control" v-model="result.deck" :disabled="result.user === ''">
                <option selected value="0">----Select One----</option>
                <option v-for="deck in result.user.decks" :value="deck.id" :key="deck.id">{{deck.name}}</option>
              </select>
            </b-col>
            <b-col md="4"></b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="12">
              <label>Points:</label>
              <input class="form-control" type="text" v-model="result.points"/>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="12">
              <label>Position:</label>
              <input class="form-control" type="text" v-model="result.position"/>
            </b-col>
          </b-row>
          <!--<b-row>
            <b-col cols="12" md="12">
              <label>Rounds played:</label>
              <input class="form-control" type="text" v-model="result.rounds"/>
            </b-col>
          </b-row>-->
          <b-row>
            <b-col cols="12" md="12">
              <label>GW (%):</label>
              <b-input-group>
                <b-input-group-text slot="append">
                  <strong>%</strong>
                </b-input-group-text>
                <b-form-input v-model="result.gwpct"></b-form-input>
              </b-input-group>
            </b-col>
          </b-row>
          <hr/>
          <b-row>
            <b-col cols="12" md="12">
              <button class="btn btn-success" @click="post">Submit</button>
            </b-col>
          </b-row>
      </b-container>
    </main>
  </section>
</template>

<script>
  /* eslint-disable */
  import Api from "../services/Api";
  export default {
    name: 'addProduct',
    data () {
      return {
        result: {
          user: '0',
          event: '0',
          deck: '0',
          points: '',
          position: '',
          rounds: '',
          gwpct: ''
        },
        showModal: false,
        selectedDeck: '',
        users: [],
        events: [],
        alerts: [],
        decks: []
      }
    },
    methods: {
      post () {
        Api().post('results/create', this.result)
          .then(res => {
            this.alerts.push({type: 'success', dismiss: 4, message: 'Result posted succesfully!'})
          })
          .catch(error => {
            this.alerts.push({type: 'danger', dismiss: 4, message: error.response.data.message})
          })
      },
      getDecks () {
        console.log(this.result.user)
       /* Api().get(`events/byUser/${this.result.user}`)
          .then(res => {
            this.events = res.data
            let user = this.users.find(x => x.id === this.result.user)
            this.decks = user.decks
          })*/
      },
      getEvents () {
        Api().get('events')
          .then(res => {
            this.events = res.data
            Api().get('users')
              .then(res => {
                this.users = res.data
              })
          })
      },
      addDeck (id, deck) {
        Api().post('decks/create', {user: id, name: deck})
          .then(res => {
            this.alerts.push({type: 'success', dismiss: 4, message: `Deck ${deck} Created Succesfully!`})
            this.result.user.decks.push(res.data.deck)
          })
          .catch(error => {
            this.alerts.push({type: 'danger', dismiss: 4, message: error.response.data.message})
          })
      }
    },
    mounted () {
      this.getEvents()
    }
  }
</script>

