              <label class="typo__label">Players in Event</label>
              <multiselect v-model="participants" :options="users" :multiple="true" :close-on-select="false" :clear-on-select="false" :preserve-search="true" placeholder="Pick some" label="username" track-by="id"></multiselect>
            </b-col>
          </b-row>-->
