<template>
  <div>
    <h4>New User</h4>
    <b-row>
      <b-col>
        <label>Username:</label>
        <input class="form-control" type="text" v-model="user"/>
      </b-col>
    </b-row>
    <hr/>
    <b-row>
      <b-col cols="12">
        <button class="btn btn-success" @click="post">Submit</button>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  /* eslint-disable */
  import Api from "../services/Api";
  export default {
    name: 'addProduct',
    data () {
      return {
        user: ''
      }
    },
    methods: {
      post () {
        Api().post('users/register', {username: this.user, results: [], decks: []})
          .then(result => {
            this.user = ''
            this.$emit('refresh')
          })
      }
    }
  }
</script>

